import React, { useEffect, useState } from 'react';
import './App.css';
import CurrentWeather from './Current_Weather';
import Forecast from './Forecast';
import cityKeys from './cityKeys';
import * as d3 from "d3";


const App = () => {

  const [city, setCity] = useState('Karachi');
  const [country, setCountry] = useState('');
  //const [coordinates, setCoordinates] = useState('');
  const [day, setDay] = useState('day');
  const [time, setTime] = useState('time');
  const [tempUnit, setTempUnit] = useState('C');
  const [temp, setTemp] = useState([0, 32]);
  const [description, setDescription] = useState('few clouds');
  const [rain, setRain] = useState(80);
  const [uv, setUV] = useState(9);
  const [wind, setWind] = useState(50);
  const [sunrise, setSunrise] = useState('6:00 AM');
  const [sunset, setSunset] = useState('7:00 PM');
  const [humidity, setHumidity] = useState(79);
  const [visibility, setVisibility] = useState(15);
  const [airQuality, setAirQuality] = useState(500);
  const [sunday, setSunday] = useState([]);
  const [monday, setMonday] = useState([]);
  const [tuesday, setTuesday] = useState([]);
  const [wednesday, setWednesday] = useState([]);
  const [thursday, setThursday] = useState([]);
  const [friday, setFriday] = useState([]);
  const [saturday, setSaturday] = useState([]);
  const [todayForecastArrF, setTodayForecastArrF] = useState([]);

  function portraitMode () {
    if(window.innerWidth<window.innerHeight){
      let CurrentWeatherMargin= document.getElementById('currentWeather_margin');
      let main_image=document.getElementById('main_image');
      let temp_time=document.getElementById('temp_time');
      let description_rain=document.getElementById('description_rain');
      let line=document.getElementById('line');
      line.style.display='none';
      let imgTempDesc= document.createElement('div');
      imgTempDesc.setAttribute('id','imgTempDesc');
      CurrentWeatherMargin.insertBefore(imgTempDesc,main_image);
      imgTempDesc.appendChild(main_image);
      imgTempDesc.appendChild(temp_time);
      imgTempDesc.appendChild(description_rain);

      let search_div=document.getElementById('search_div');
      let tempUnit_dp=document.getElementById('tempUnit_dp');
      let search_tempUnitDP=document.createElement('div');
      search_tempUnitDP.setAttribute('id','search_tempUnitDP');
      CurrentWeatherMargin.insertBefore(search_tempUnitDP,search_div);
      search_tempUnitDP.appendChild(search_div);
      search_tempUnitDP.appendChild(tempUnit_dp);
    }
  }
  // window.addEventListener("resize",portraitMode, false);
  // window.removeEventListener("resize",portraitMode);
  window.onload = (event) => {
    portraitMode();
    getLocation();
  }

  const getLocation = () => {
    let lat, long;
    navigator.geolocation.getCurrentPosition((position) => {
      lat = position.coords.latitude;
      long = position.coords.longitude;
      fetch('https://us1.locationiq.com/v1/reverse.php?key=fa39ae0525cd2e&lat=' + lat + '&lon=' + long + '&format=json').then(response => response.json()).then(response => {
        updateWeather(response.address.city);
        todayForecast(response.address.city);
        updateTime(response.address.city);
      }).catch((error) => {
        console.log(error);
      });
    });

  }
  const updateWeather = (city) => {
    const proxyurl = "https://cors-anywhere.herokuapp.com/";
    fetch(proxyurl + 'https://api.weatherapi.com/v1/forecast.json?key=1e4d592ccf9047c7a68192207201407&q=' + city + '&days=10').then(response => response.json()).then((response) => {
      console.log(response);
      setCity(response.location.name);
      setCountry(response.location.country);
      let tempArr = [];
      tempArr.push(Math.round(response.current.temp_c), Math.round(response.current.temp_f));
      setTemp([...tempArr]);
      setDescription(response.current.condition.text);
      setHumidity(response.current.humidity);
      setUV(response.current.uv);
      setWind(response.current.wind_kph);
      setSunrise(response.forecast.forecastday[0].astro.sunrise);
      setSunset(response.forecast.forecastday[0].astro.sunset);
      setVisibility(response.current.vis_km);
      setRain(response.forecast.forecastday[0].day.daily_chance_of_rain);


      let forecastArr = [];
      let daysArr = [];
      response.forecast.forecastday.map((x) => {
        let date = new Date(x.date);
        let day = date.getDay();
        daysArr.push(day);
        switch (day) {

          case 0:
            forecastArr = [];
            forecastArr.push(x.day.condition.text, Math.round(x.day.mintemp_c), Math.round(x.day.mintemp_f), Math.round(x.day.maxtemp_c), Math.round(x.day.maxtemp_f));
            setSunday([...forecastArr]);
            break;

          case 1:
            forecastArr = [];
            forecastArr.push(x.day.condition.text, Math.round(x.day.mintemp_c), Math.round(x.day.mintemp_f), Math.round(x.day.maxtemp_c), Math.round(x.day.maxtemp_f));
            setMonday([...forecastArr]);
            break;

          case 2:
            forecastArr = [];
            forecastArr.push(x.day.condition.text, Math.round(x.day.mintemp_c), Math.round(x.day.mintemp_f), Math.round(x.day.maxtemp_c), Math.round(x.day.maxtemp_f));
            setTuesday([...forecastArr]);
            break;

          case 3:
            forecastArr = [];
            forecastArr.push(x.day.condition.text, Math.round(x.day.mintemp_c), Math.round(x.day.mintemp_f), Math.round(x.day.maxtemp_c), Math.round(x.day.maxtemp_f));
            setWednesday([...forecastArr]);
            break;

          case 4:
            forecastArr = [];
            forecastArr.push(x.day.condition.text, Math.round(x.day.mintemp_c), Math.round(x.day.mintemp_f), Math.round(x.day.maxtemp_c), Math.round(x.day.maxtemp_f));
            setThursday([...forecastArr]);
            break;

          case 5:
            forecastArr = [];
            forecastArr.push(x.day.condition.text, Math.round(x.day.mintemp_c), Math.round(x.day.mintemp_f), Math.round(x.day.maxtemp_c), Math.round(x.day.maxtemp_f));
            setFriday([...forecastArr]);
            break;

          case 6:
            forecastArr = [];
            forecastArr.push(x.day.condition.text, Math.round(x.day.mintemp_c), Math.round(x.day.mintemp_f), Math.round(x.day.maxtemp_c), Math.round(x.day.maxtemp_f));
            setSaturday([...forecastArr]);
            break;

        }
      });
      let descriptionsArr = ['Clear Sky',
        'Sunny',
        'Scattered clouds',
        'few clouds',
        'light intensity drizzle',
        'smoke',
        'broken clouds',
        'haze',
        'light rain',
        'Thunderstorm'
      ]
      let randomTempC = 0;
      let randomTempF = 0;
      let daysArray = [0, 1, 2, 3, 4, 5, 6];
      daysArr.map(x => {
        if (daysArray.includes(x)) {
          daysArray.splice(daysArray.indexOf(x), 1);
        }
      })
      let tempRandom = [Math.round(response.current.temp_c), Math.round(response.current.temp_f)];
      daysArray.map((x) => {

        switch (x) {

          case 0:
            forecastArr = [];
            randomTempC = Math.round((Math.random() * 10) + 1 + (tempRandom[0] - 5));
            randomTempF = Math.round((Math.random() * 10) + 1 + (tempRandom[1] - 5));
            forecastArr.push(descriptionsArr[Math.random() * 10], randomTempC - 3, randomTempF - 3, randomTempC, randomTempF);
            setSunday([...forecastArr]);
            break;

          case 1:
            forecastArr = [];
            randomTempC = Math.round((Math.random() * 10) + 1 + (tempRandom[0] - 5));
            randomTempF = Math.round((Math.random() * 10) + 1 + (tempRandom[1] - 5));
            forecastArr.push(descriptionsArr[Math.random() * 10], randomTempC - 3, randomTempF - 3, randomTempC, randomTempF);
            setMonday([...forecastArr]);
            break;

          case 2:
            forecastArr = [];
            randomTempC = Math.round((Math.random() * 10) + 1 + (tempRandom[0] - 5));
            randomTempF = Math.round((Math.random() * 10) + 1 + (tempRandom[1] - 5));
            forecastArr.push(descriptionsArr[Math.random() * 10], randomTempC - 3, randomTempF - 3, randomTempC, randomTempF);
            setTuesday([...forecastArr]);
            break;

          case 3:
            forecastArr = [];
            randomTempC = Math.round((Math.random() * 10) + 1 + (tempRandom[0] - 5));
            randomTempF = Math.round((Math.random() * 10) + 1 + (tempRandom[1] - 5));
            forecastArr.push(descriptionsArr[Math.random() * 10], randomTempC - 3, randomTempF - 3, randomTempC, randomTempF);
            setWednesday([...forecastArr]);
            break;

          case 4:
            forecastArr = [];
            randomTempC = Math.round((Math.random() * 10) + 1 + (tempRandom[0] - 5));
            randomTempF = Math.round((Math.random() * 10) + 1 + (tempRandom[1] - 5));
            forecastArr.push(descriptionsArr[Math.random() * 10], randomTempC - 3, randomTempF - 3, randomTempC, randomTempF);
            setThursday([...forecastArr]);
            break;

          case 5:
            forecastArr = [];
            randomTempC = Math.round((Math.random() * 10) + 1 + (tempRandom[0] - 5));
            randomTempF = Math.round((Math.random() * 10) + 1 + (tempRandom[1] - 5));
            forecastArr.push(descriptionsArr[Math.random() * 10], randomTempC - 3, randomTempF - 3, randomTempC, randomTempF);
            setFriday([...forecastArr]);
            break;

          case 6:
            forecastArr = [];
            randomTempC = Math.round((Math.random() * 10) + 1 + (tempRandom[0] - 5));
            randomTempF = Math.round((Math.random() * 10) + 1 + (tempRandom[1] - 5));
            forecastArr.push(descriptionsArr[Math.random() * 10], randomTempC - 3, randomTempF - 3, randomTempC, randomTempF);
            setSaturday([...forecastArr]);
            break;

        }
      });



    }).catch((error) => {
      console.log(error);
    })
  }
  const calcTime = (offset) => {

    // create Date object for current location
    let d = new Date();

    // convert to msec
    // add local time zone offset
    // get UTC time in msec
    let utc = d.getTime() + (d.getTimezoneOffset() * 60000);

    // create new Date object for different city
    // using supplied offset
    let nd = new Date(utc + (3600000 * offset));

    // return time as a string
    return nd;

  }
  const updateTime = (area) => {

    fetch('https://api.ipgeolocation.io/timezone?apiKey=d95d71f83ba9415ea966457a35fbab79&location=' + area).then(response => response.json()).then((response) => {
      let date = calcTime(response.timezone_offset);
      let day = date.getDay();
      switch (day) {
        case 0: day = 'Sunday';
          break;
        case 1: day = 'Monday';
          break;
        case 2: day = 'Tuesday';
          break;
        case 3: day = 'Wednesday';
          break;
        case 4: day = 'Thursday';
          break;
        case 5: day = 'Friday';
          break;
        case 6: day = 'Saturday';
          break;
      }
      setDay(day);
      let hours = date.getHours() + response.dst_savings;
      let minutes = date.getMinutes();
      minutes = minutes < 10 ? '0' + minutes : minutes;
      let timeData = hours + ':' + minutes;
      setTime(timeData);

    }).catch((error) => {
      console.log(error);
    });

  }

  const setCelsius = () => {
    setTempUnit('C');
    if (todayForecastArrF[0]) {
      document.getElementById('todaysvg').innerHTML = '';
      if (cityKeys.hasOwnProperty(city)) {
        let width = '420', height = '80', axisBottomHeight = '85';
        var svg = d3.select('#todaysvg');
        var g = svg.append("g").attr("transform", "translate(20,15)");
        var x = d3.scaleTime().domain([todayForecastArrF[0].time, todayForecastArrF[11].time]).range([0, width])
        var y = d3.scaleLinear().domain([d3.min(todayForecastArrF, d => d.temp), d3.max(todayForecastArrF, d => d.temp)]).range([height, 0])
        var line = d3.line().x(function (d) { return x(d.time) }).y(function (d) { return y(d.temp) });
        g.append("g").attr("transform", "translate(0," + axisBottomHeight + ")").call(d3.axisBottom(x)).select(".domain").remove();

        g.append("path").datum(todayForecastArrF).attr("fill", "none").attr("stroke", "steelblue").attr("stroke-linejoin", "round").attr("stroke-linecap", "round").attr("stroke-width", 1.5).attr("d", line);

        svg.append('g').attr("transform", "translate(15,7)").selectAll('text')
          .data(todayForecastArrF)
          .enter()
          .append('text')
          .text((d) => Math.round((d.temp - 32) * 5 / 9))
          .attr('x', (d, i) => x(d.time))
          .attr('y', (d) => y(d.temp))
          .attr('font-size', '10');
      }
    }
  }
  const setFahrenheight = () => {
    setTempUnit('F');
    if (todayForecastArrF[0]) {
      document.getElementById('todaysvg').innerHTML = '';
      if (cityKeys.hasOwnProperty(city)) {
        let width = '420', height = '80', axisBottomHeight = '85';
        var svg = d3.select('#todaysvg');
        var g = svg.append("g").attr("transform", "translate(20,15)");
        var x = d3.scaleTime().domain([todayForecastArrF[0].time, todayForecastArrF[11].time]).range([0, width])
        var y = d3.scaleLinear().domain([d3.min(todayForecastArrF, d => d.temp), d3.max(todayForecastArrF, d => d.temp)]).range([height, 0])
        var line = d3.line().x(function (d) { return x(d.time) }).y(function (d) { return y(d.temp) });
        g.append("g").attr("transform", "translate(0," + axisBottomHeight + ")").call(d3.axisBottom(x)).select(".domain").remove();

        g.append("path").datum(todayForecastArrF).attr("fill", "none").attr("stroke", "steelblue").attr("stroke-linejoin", "round").attr("stroke-linecap", "round").attr("stroke-width", 1.5).attr("d", line);

        svg.append('g').attr("transform", "translate(15,7)").selectAll('text')
          .data(todayForecastArrF)
          .enter()
          .append('text')
          .text((d) => Math.round(d.temp))
          .attr('x', (d, i) => x(d.time))
          .attr('y', (d) => y(d.temp))
          .attr('font-size', '10');
      }
    }
  }

  const todayForecast = (city) => {
    document.getElementById('todaysvg').innerHTML = '';
    if (cityKeys.hasOwnProperty(city)) {

      fetch('http://dataservice.accuweather.com/forecasts/v1/hourly/12hour/' + cityKeys[city] + '?apikey=IskrHnBYGaIjjyeKUaZdb1Es0B0BVlU2').then(response => response.json()).then((response) => {

        let todayForecastArr = [];
        let time;
        response.map(x => {
          time = x.DateTime.slice(0, 19);

          todayForecastArr.push({ time: new Date(time), temp: x.Temperature.Value });
        });
        setTodayForecastArrF([...todayForecastArr]);
        let vh= window.innerHeight;
        let width = 0.72*vh, height = 0.128*vh, axisBottomHeight = 0.136*vh;
        var svg = d3.select('#todaysvg');
        var g = svg.append("g").attr("transform", "translate("+0.024*vh+","+0.024*vh+")");
        var x = d3.scaleTime().domain([todayForecastArr[0].time, todayForecastArr[11].time]).range([0, width])
        var y = d3.scaleLinear().domain([d3.min(todayForecastArr, d => d.temp), d3.max(todayForecastArr, d => d.temp)]).range([height, 0])
        var line = d3.line().x(function (d) { return x(d.time) }).y(function (d) { return y(d.temp) });
        g.append("g").attr("transform", "translate(0," + axisBottomHeight + ")").call(d3.axisBottom(x)).select(".domain").remove();

        g.append("path").datum(todayForecastArr).attr("fill", "none").attr("stroke", "steelblue").attr("stroke-linejoin", "round").attr("stroke-linecap", "round").attr("stroke-width", 1.5).attr("d", line);

        svg.append('g').attr("transform", "translate("+0.016*vh+","+0.0112*vh+")").selectAll('text')
          .data(todayForecastArr)
          .enter()
          .append('text')
          .text((d) => { if (tempUnit == 'C') { return Math.round((d.temp - 32) * 5 / 9) } else if (tempUnit == 'F') { return Math.round(d.temp) } })
          .attr('x', (d, i) => x(d.time))
          .attr('y', (d) => y(d.temp))
          .attr('font-size', '10');



      }).catch((error) => {
        console.log(error);
      })
    }
  }



  const handleCity = (e) => {
    let area;
    document.getElementById('search').value = '';
    area = e.target.innerText;
    setCity(area);
    updateTime(area);
    updateWeather(area);
    hideSearchResults();
    todayForecast(area);

  }

  const hideSearchResults = () => {
    var x = document.getElementById("searchResults");
    x.style.display = "none";
    let searchBar = document.getElementById('searchBar');
    searchBar.style.border = "2px solid white";
  }



  return (
    <div id='main'>
      <div id="main_container">
        <CurrentWeather getLocation={getLocation} handleCity={handleCity} description={description} temp={temp} tempUnit={tempUnit} rain={rain} day={day} time={time} city={city} country={country} />
        <Forecast setCelsius={setCelsius} setFahrenheight={setFahrenheight} sunday={sunday} monday={monday} tuesday={tuesday} wednesday={wednesday} thursday={thursday} friday={friday} saturday={saturday} tempUnit={tempUnit} uv={uv} wind={wind} sunrise={sunrise} sunset={sunset} humidity={humidity} visibility={visibility} airQuality={airQuality} description={description} />
      </div>
    </div>
  );
}

export default App;
