import React from 'react';

import clearSky from './Design/Weather Icons/Clear Sky.png';
import scatteredClouds from './Design/Weather Icons/Scattered Clouds.png';
import fewClouds from './Design/Weather Icons/Few Clouds.png';
import lightIntensityDrizzle from './Design/Weather Icons/Light Intensity Drizzle.png';
import smoke from './Design/Weather Icons/Smoke.jpg';
import brokenClouds from './Design/Weather Icons/Broken Clouds.png';
import haze from './Design/Weather Icons/Haze.png';
import defaultIcon from './Design/Weather Icons/question.png';

const WeatherIcon = ({ description, id }) => {
    let icon;
    switch (description.toLowerCase()) {
        case 'sunny': icon = clearSky;
            break;
        case 'scattered clouds': icon = scatteredClouds;
            break;
        case 'partly cloudy': icon = fewClouds;
            break;
        case 'broken clouds': icon = brokenClouds;
            break;
        case 'light intensity drizzle': icon = lightIntensityDrizzle;
            break;
        case 'smoke': icon = smoke;
            break;
        case 'haze': icon = haze;
            break;
        default: icon = defaultIcon;
    }
    return (
        <img src={icon} className='weatherIcons' id={id} alt='Weather Icon' />
    )

}

export default WeatherIcon;