import React, { useEffect, useState } from 'react';
import cities from './cities0';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { fas, faSearch, faMapMarkerAlt, faTint, faSmog } from '@fortawesome/free-solid-svg-icons'
import place from './Design/Cities/city scape.png';
import WeatherIcon from './WeatherIcon';
import search from './Design/Icons/search.png';
import locate from './Design/Icons/locate.png';
import descriptonIcon from './Design/Icons/descriptionIcon.png';
import rainIcon from './Design/Icons/rain.png';
import sad from './Design/Icons/sad smiley.png';
import thumbsUp from './Design/Icons/thumbs up.png';
import thumbsDown from './Design/Icons/thumbs down.png';
import wind from './Design/Icons/wind.png';



const CurrentWeather = ({ getLocation, handleCity, temp, tempUnit, description, rain, day, time, city, country }) => {

    const [filteredCities, setFilteredCities] = useState([]);


    const handleSearch = (e) => {

        let searchResults = [];
        const countries = Object.keys(cities);
        if (document.getElementById('search').value.length > 0) {
            searchResults = [];
            for (let i = 0; i < countries.length; i++) {
                cities[countries[i]].map(city => {
                    if (city.toLowerCase().includes(e.target.value.toLowerCase())) {
                        searchResults.push(city);
                    }
                })
            };
        };
        setFilteredCities([]);
        setFilteredCities([...searchResults]);

        var x = document.getElementById("searchResults");
        if (searchResults.length > 0) {
            x.style.display = "block";
        } else {
            x.style.display = "none";
        }
    }

    const showSearchBar = () => {
        let searchBar = document.getElementById('searchBar');
        if(window.innerWidth<window.innerHeight){
            searchBar.style.border = "0.5vw solid #b8c9e6";
        }
        else{
        searchBar.style.border = "0.4vh solid #b8c9e6";
        }
    }

    const hideSearchBar = () => {
        let searchBar = document.getElementById('searchBar');
        if(window.innerWidth<window.innerHeight){
            searchBar.style.border = "0.5vw solid rgb(212, 219, 231)";
        }
       else{
        searchBar.style.border = "0.4vh solid white";
       }

    }

    const hideSearchResults = () => {
        var x = document.getElementById("searchResults");
        x.style.display = "none";
        hideSearchBar();
    }

    return (

        < div id='current_weather' >
            <div id='currentWeather_margin'>
                <div id='search_div'>
                    <div id='searchBar'><img src={search} id='faSearch' />
                        <input id='search' type='text' onFocus={showSearchBar} onBlur={hideSearchBar} onChange={handleSearch} placeholder='Search for places ...' />
                        <a id='faMapMarkerLink' href='#' onClick={getLocation}><img src={locate} id='faMapMarkerAlt' /></a>
                    </div>


                    <div id='searchResults' tabIndex="1" onBlur={hideSearchResults}>
                        <ul id='searchList'>
                            {filteredCities.map((x, i) => (
                                <li key={i} className='searchListItems' onClick={handleCity}><a className='searchListLinks' href='#' onClick={handleCity}>{x}</a> </li>
                            ))}
                        </ul>

                    </div>
                </div>

                <WeatherIcon description={description} id='main_image' />
                <div id='temp_time'>

                    {tempUnit === 'C' && <div><h1 className='temperature'>{temp[0]}</h1><h1 className='degree'><sup>{String.fromCharCode(176)}C</sup></h1></div>}
                    {tempUnit === 'F' && <div><h1 className='temperature'>{temp[1]}</h1><h2 className='degree'><sup>{String.fromCharCode(176)}F</sup></h2></div>}

                    <p id='day_time'><span id='day'>{day}, </span><span id='time'>{time}</span></p>
                </div>
                <div id='line'></div>
                <div id='description_rain'>
                    <p id='description'><img src={descriptonIcon} id='descriptonIcon' />{description}</p>
                    <p id='rain'><img src={rainIcon} id='rainIcon' />Rain - {rain}%</p>
                </div>
                <div id='city_div'>
                    <span id='cityName'>{city}, {country}</span>
                    <img id='city_image' src={place}></img></div>
            </div>
        </div >
    )
}

export default CurrentWeather;
