import React from 'react';
import DP from './Design/Profiles/ibrahim.jpg';
import WeatherIcon from './WeatherIcon';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faLocationArrow, faDotCircle, faArrowUp, faArrowDown } from '@fortawesome/free-solid-svg-icons';
import * as d3 from "d3";

const Forecast = ({ setCelsius, setFahrenheight, sunday, monday, tuesday, wednesday, thursday, friday, saturday, tempUnit, uv, wind, sunrise, sunset, humidity, visibility, airQuality, description }) => {




    const handleToday = () => {

        let today = document.getElementById('today')
        today.style.color = 'black';
        today.style.borderBottom = '0.16vh solid black';
        let week = document.getElementById('week')
        week.style.color = '#C5C5C5';
        week.style.borderBottom = 'none';

        document.getElementById('todayForecast').style.display = 'block';
        document.getElementById('temperature_widgets').style.display = 'none';
    }

    const handleWeek = () => {
        let week = document.getElementById('week')
        week.style.color = 'black';
        week.style.borderBottom = '0.16vh solid black';
        let today = document.getElementById('today')
        today.style.color = '#C5C5C5';
        today.style.borderBottom = 'none';

        document.getElementById('temperature_widgets').style.display = 'grid';
        document.getElementById('todayForecast').style.display = 'none';
    }
    const handleCelsius = () => {
        let celsiusCircle = document.getElementById('celsius_circle')
        celsiusCircle.style.backgroundColor = '#1A1A1A';
        let celsius = document.getElementById('celsius');
        celsius.style.color = 'white';
        let fahrenheightCircle = document.getElementById('fahrenheight_circle')
        fahrenheightCircle.style.backgroundColor = 'white';
        let fahrenheight = document.getElementById('fahrenheight');
        fahrenheight.style.color = 'black';
        setCelsius();
    }
    const handleFahrenheight = () => {
        let fahrenheightCircle = document.getElementById('fahrenheight_circle')
        fahrenheightCircle.style.backgroundColor = '#1A1A1A';
        let fahrenheight = document.getElementById('fahrenheight');
        fahrenheight.style.color = 'white';
        let celsiusCircle = document.getElementById('celsius_circle')
        celsiusCircle.style.backgroundColor = 'white';
        let celsius = document.getElementById('celsius');
        celsius.style.color = 'black';
        setFahrenheight();


    }



    let vh= window.innerHeight;
    var datum = [1, 1, 1, 1];
    let labels = [3, 6, 9, 12];
    var svgPath = d3.select("#svgPath"),
        r = 0.096*vh,
        gPath = svgPath.append("g").attr("transform", "translate("+r+","+0.12*vh+")")


    var colorPath = d3.scaleOrdinal(['#F3F3F4']);

    // Generate the pie
    var piePath = d3.pie()
        .startAngle(Math.PI / 2)
        .endAngle(-Math.PI / 2);

    // Generate the arcs
    var arcPath = d3.arc()
        .innerRadius(r - 0.016*vh)
        .outerRadius(r - 0.0064*vh)


    //Generate groups
    var arcsPath = gPath.selectAll("arc")
        .data(piePath(datum))
        .enter()
        .append("g")
        .attr("class", "arcPath")

    //Draw arc paths
    arcsPath.append("path")
        .attr("fill", function (d, i) {
            return colorPath(i);
        })
        .attr("d", arcPath);





    var data = [];
    data.push(uv, 15 - uv);

    var svg = d3.select("#svg"),
        radius = 0.096*vh,
        g = svg.append("g").attr("transform", "translate("+r+","+0.12*vh+")")


    var color = d3.scaleOrdinal(['#FFBF5E', 'transparent']);

    // Generate the pie

    var pie = uv < 8 ? d3.pie()
        .startAngle(Math.PI / 2)
        .endAngle(-Math.PI / 2) : d3.pie()
            .startAngle(-Math.PI / 2)
            .endAngle(Math.PI / 2)

    // Generate the arcs
    var arc = d3.arc()
        .innerRadius(radius - 0.0224*vh)
        .outerRadius(radius)


    //Generate groups
    var arcs = g.selectAll("arc")
        .data(pie(data))
        .enter()
        .append("g")
        .attr("class", "arc")

    //Draw arc paths
    arcs.append("path")
        .attr("fill", function (d, i) {
            return color(i);
        })
        .attr("d", arc);


    var humidHeight = 0.08*vh;
    var graph = d3.select("#humiditySvg")
        .attr("width", 0.024*vh)
        .attr("height", humidHeight);



    d3.select("#humidityCircle")
        .attr('r', 0.008*vh)
        .attr('cx', 0.012*vh)
        .attr('cy', humidHeight +(0.012*vh) - (humidHeight * humidity * 0.01))
        .attr('fill', '#4050D2')

    const airHeight = humidHeight;
    d3.select('#airCircle')
        .attr('cy', airHeight  +(0.012*vh) - (airHeight * airQuality / 500))
    return (
        <div id='forecast'>
            <div id='forecast_margin'>
                <div id='header'>
                    <div id='today_week'>
                        <a href='#' id='today' onClick={handleToday}>Today</a>

                        <a href='#' id='week' onClick={handleWeek}>Week</a>
                    </div>
                    <div id='tempUnit_dp'>
                        <div id='temp_toggle'>
                            <a id='celsius' href='#' onClick={handleCelsius}><div className='circle' id='celsius_circle'></div>{String.fromCharCode(176)}C</a>
                            <a id='fahrenheight' href='#' onClick={handleFahrenheight}><div className='circle' id='fahrenheight_circle'></div>{String.fromCharCode(176)}F</a>
                        </div>
                        <img id='display_picture' src={DP} alt='Profile Picture' />
                    </div>
                </div>
                <div id='temperature_widgets'>
                    <div className='days'><h3>Sun</h3>
                        <WeatherIcon description={description} />
                        <div className='tempC_tempF'>
                            {(tempUnit === 'C' && sunday[3]) && <span className='tempC' className='maxTemp'>{sunday[3] + String.fromCharCode(176)}</span>}
                            {(tempUnit === 'F' && sunday[4]) && <span className='tempF' className='maxTemp' >{sunday[4] + String.fromCharCode(176)}</span>}
                            {(tempUnit === 'C' && sunday[1]) && <span className='tempC' className='minTemp'>{sunday[1] + String.fromCharCode(176)}</span>}
                            {(tempUnit === 'F' && sunday[2]) && <span className='tempF' className='minTemp'>{sunday[2] + String.fromCharCode(176)}</span>}
                            {!sunday[0] && <span className='notAvailable'>Not Available</span>}
                        </div>
                    </div>
                    <div className='days'>
                        <h3>Mon</h3>
                        <WeatherIcon description={description} />
                        <div className='tempC_tempF'>
                            {(tempUnit === 'C' && monday[3]) && <span className='tempC' className='maxTemp'>{monday[3] + String.fromCharCode(176)}</span>}
                            {(tempUnit === 'F' && monday[4]) && <span className='tempF' className='maxTemp' >{monday[4] + String.fromCharCode(176)}</span>}
                            {(tempUnit === 'C' && monday[1]) && <span className='tempC' className='minTemp'>{monday[1] + String.fromCharCode(176)}</span>}
                            {(tempUnit === 'F' && monday[2]) && <span className='tempF' className='minTemp'>{monday[2] + String.fromCharCode(176)}</span>}
                            {!sunday[0] && <span className='notAvailable'>Not Available</span>}</div></div>
                    <div className='days'>
                        <h3>Tue</h3>
                        <WeatherIcon description={description} />
                        <div className='tempC_tempF'>
                            {(tempUnit === 'C' && tuesday[3]) && <span className='tempC' className='maxTemp'>{tuesday[3] + String.fromCharCode(176)}</span>}
                            {(tempUnit === 'F' && tuesday[4]) && <span className='tempF' className='maxTemp' >{tuesday[4] + String.fromCharCode(176)}</span>}
                            {(tempUnit === 'C' && tuesday[1]) && <span className='tempC' className='minTemp'>{tuesday[1] + String.fromCharCode(176)}</span>}
                            {(tempUnit === 'F' && tuesday[2]) && <span className='tempF' className='minTemp'>{tuesday[2] + String.fromCharCode(176)}</span>}
                            {!sunday[0] && <span className='notAvailable'>Not Available</span>}</div></div>
                    <div className='days'>
                        <h3>Wed</h3>
                        <WeatherIcon description={description} />
                        <div className='tempC_tempF'>
                            {(tempUnit === 'C' && wednesday[3]) && <span className='tempC' className='maxTemp'>{wednesday[3] + String.fromCharCode(176)}</span>}
                            {(tempUnit === 'F' && wednesday[4]) && <span className='tempF' className='maxTemp'>{wednesday[4] + String.fromCharCode(176)}</span>}
                            {(tempUnit === 'C' && wednesday[1]) && <span className='tempC' className='minTemp'>{wednesday[1] + String.fromCharCode(176)}</span>}
                            {(tempUnit === 'F' && wednesday[2]) && <span className='tempF' className='minTemp'>{wednesday[2] + String.fromCharCode(176)}</span>}
                            {!sunday[0] && <span className='notAvailable'>Not Available</span>}</div></div>
                    <div className='days'>
                        <h3>Thu</h3>
                        <WeatherIcon description={description} />
                        <div className='tempC_tempF'>
                            {(tempUnit === 'C' && thursday[3]) && <span className='tempC' className='maxTemp'>{thursday[3] + String.fromCharCode(176)}</span>}
                            {(tempUnit === 'F' && thursday[4]) && <span className='tempF' className='maxTemp' >{thursday[4] + String.fromCharCode(176)}</span>}
                            {(tempUnit === 'C' && thursday[1]) && <span className='tempC' className='minTemp'>{thursday[1] + String.fromCharCode(176)}</span>}
                            {(tempUnit === 'F' && thursday[2]) && <span className='tempF' className='minTemp'>{thursday[2] + String.fromCharCode(176)}</span>}
                            {!sunday[0] && <span className='notAvailable'>Not Available</span>}</div></div>
                    <div className='days'>
                        <h3>Fri</h3>
                        <WeatherIcon description={description} />
                        <div className='tempC_tempF'>
                            {(tempUnit === 'C' && friday[3]) && <span className='tempC' className='maxTemp'>{friday[3] + String.fromCharCode(176)}</span>}
                            {(tempUnit === 'F' && friday[4]) && <span className='tempF' className='maxTemp' >{friday[4] + String.fromCharCode(176)}</span>}
                            {(tempUnit === 'C' && friday[1]) && <span className='tempC' className='minTemp'>{friday[1] + String.fromCharCode(176)}</span>}
                            {(tempUnit === 'F' && friday[2]) && <span className='tempF' className='minTemp'>{friday[2] + String.fromCharCode(176)}</span>}
                            {!sunday[0] && <span className='notAvailable'>Not Available</span>}</div></div>
                    <div className='days'>
                        <h3>Sat</h3>
                        <WeatherIcon description={description} />
                        <div className='tempC_tempF'>
                            {(tempUnit === 'C' && saturday[3]) && <span className='tempC' className='maxTemp'>{saturday[3] + String.fromCharCode(176)}</span>}
                            {(tempUnit === 'F' && saturday[4]) && <span className='tempF' className='maxTemp' >{saturday[4] + String.fromCharCode(176)}</span>}
                            {(tempUnit === 'C' && saturday[1]) && <span className='tempC' className='minTemp'>{saturday[1] + String.fromCharCode(176)}</span>}
                            {(tempUnit === 'F' && saturday[2]) && <span className='tempF' className='minTemp'>{saturday[2] + String.fromCharCode(176)}</span>}
                            {!sunday[0] && <span className='notAvailable'>Not Available</span>}</div></div>
                </div>

                <div id='todayForecast'>
                    <svg id='todaysvg'></svg>
                </div>

                <div>
                    <h2 id='highlights'>Today's Highlights</h2>
                    <div id='highlights_widgets'>

                        <div className='highlights'>
                            <div id='uvMargin' className='divMargin'>
                                <h3>UV Index</h3>
                             
                                    <svg id='svgPath'> </svg>
                               
                             
                                    <svg id='svg' > </svg>
                                

                                <span class='labels' id='six'>6</span>
                                <span class='labels' id='nine'>9</span>
                                <span class='labels' id='twelve'>12</span>
                                <span id='uv'>{uv}</span>


                            </div>
                        </div>
                        <div className='highlights'>
                            <div id='windMargin' className='divMargin'>
                                <h3>Wind Status</h3>
                                <p id='wind'><span>{wind}</span> km/h</p>
                                <span id='wsw'><FontAwesomeIcon icon={faDotCircle} id='faCircle' />WSW</span>
                            </div>
                        </div>
                        <div className='highlights'>
                            <div id='sunRiseRestMargin' className='divMargin'>
                                <h3 id='sunriseRestHead'>Sunrise {String.fromCharCode(38)} Sunset</h3>
                                <div id='sunriseSetBlock'>
                                <div id='sunrise'>
                                    <snap id='faArrowUpSnap'><div id='faArrowUpCircle' class='circle'></div><FontAwesomeIcon id='faArrowUp' icon={faArrowUp} /></snap>
                                    <div>
                                        <p id='sunriseTime'>{sunrise}</p>
                                        <snap id='sunriseError'>- 1m 46s</snap>
                                    </div>
                                </div>
                                <div id='sunset'>
                                    <snap id='faArrowDownSnap'><div id='faArrowDownCircle' class='circle'></div><FontAwesomeIcon id='faArrowDown' icon={faArrowDown} /></snap>
                                    <div>
                                        <p id='sunsetTime'>{sunset}</p>
                                        <snap id='sunsetError'>+ 2m 22s</snap>
                                    </div>
                                </div>
                                </div>
                            </div>
                        </div>
                        <div className='highlights'>
                            <div class='divMargin' id='humidityMargin'>
                                <h3>Humidity</h3>
                                <p id='humidity'>{humidity}<sup>%</sup></p>
                                <snap id='humidityDescription'>{humidity <= 20 ? 'Dry' : humidity > 20 && humidity <= 60 ? 'Normal' : 'Wet'}</snap>
                                <svg id='humiditySvg'>
                                    <circle id='humidityCircle'></circle>
                                </svg>
                            </div>
                        </div>
                        <div className='highlights'>
                            <div className='divMargin'>
                                <h3>Visibility</h3>
                                <p id='visibility'>{visibility}<snap>km</snap></p>
                                <span id='visibilityDescription'>{visibility < 0.1 ? 'Zero' : visibility >= 0.1 && visibility <= 0.4 ? 'Low' : visibility > 0.4 && visibility <= 15 ? 'Average' : 'Clear'}</span>
                            </div>
                        </div>
                        <div className='highlights'>
                            <div class='divMargin' id='airMargin'>
                                <h3>Air Quality</h3>
                                <p id='air'>{airQuality}</p>
                                <snap id='airDescription'>{airQuality <= 50 ? 'Healthy' : airQuality > 50 && airQuality <= 100 ? 'Moderate' : airQuality > 100 && airQuality <= 150 ? 'Unhealthy for sensitive groups' : airQuality > 150 && airQuality <= 200 ? 'Unhealthy' : airQuality > 200 && airQuality <= 300 ? 'Very unhealthy' : 'Hazardous'}</snap>
                                <svg id='airSvg'>
                                    <circle id='airCircle' r={0.008*vh} cx={0.012*vh} fill='#4050D2'></circle>
                                </svg>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div >
    )
}

export default Forecast;